import React, { Component } from 'react';

import './App.css';
//import Resultados from './Resultados';
//import customData from './data.json';
import $ from 'jquery'

class App extends Component {

    constructor(props){
        super(props)

        this.test= function(){

           props.hola( {

            ano:$("#ano").val(),
            comuna:$("#comuna").val(),
            colegio:$("#colegio").val(),        
           })


        }
        this.logChange = function() {
    console.log('entered');
            }


    }

  render() {
    return (  <form className="main-search border-less-inputs" >
            <div className="input-row">
                <div className="form-group">
                    <select name="category" id="ano"   multiple title="Año de egreso">
                        <option value={2014}>2014</option>
                        <option value={2015}>2015</option>
                    </select>
                </div>
                <div className="form-group">
                    <select name="category" id="comuna"   multiple title="Seleccionar comuna" >
                        <option value="COLINA">Colina</option>
                        <option value="Til Til">Til Til</option>
                    </select>
                </div>
                {/* /.form-group */}
                <div className="form-group">
                    <select name="category" id="colegio"  multiple title="Seleccionar colegio" >
                        <option value="LICEO POLIVALENTE ESMERALDA">LICEO POLIVALENTE ESMERALDA</option>
                        <option value="Huertos Familiares">Huertos Familiares</option>
                    </select>
                </div>
                {/* /.form-group */}
                <div className="form-group">
                    <a type="submit" onClick={this.test} className="btn btn-default"><i className="fa fa-search" /></a>
                </div>
                {/* /.form-group */}
            </div>
            {/* /.input-row */}
        </form>
    );
  }
}

export default App;



