import React, { Component } from 'react';


class Resultados extends Component {

    constructor(props){
        super(props);


        var get_datos = function () {

         return props.resultados.map(function (data,ixx ) {
            var movemapa = function () {


                if (data.latitude!="0" && data.latitude!="" ) {
                    var myLatLng = {lat: +data.latitude, lng: +data.longitude};
                    window.map.setCenter(myLatLng)
                    window.map.setZoom(18)

                }
                else{

                    alert("Su direccion no es convertible en coordenadas")
                }

             }

            return <li key={ixx}>
                <div className="item" id={1} >
                    <div className="wrapper"><a onClick={movemapa} id={1}><h3>{data["Nombre completo"]}</h3></a>
                        <p>Telefono {data.Telefono}</p>
                        <p>Rut {data.RUT}</p>
                        <p>Titulo {data.TITULO}</p>
                        <p>Año {data["AÑO"]}</p>
                        <p>Dirección {data["Dirección"]}</p>
                        <p>ESTABLECIMIENTO {data["ESTABLECIMIENTO"]}</p>
                    </div>
                </div>
            </li>
        });
        }

        this.states={datos:get_datos()}


    }

    componentWillReceiveProps(nextProps) {

    console.log('componentWillMount');

        var get_datos = function () {

            return nextProps.resultados.map(function (data,ixx ) {

                return <li key={ixx}>
                    <div className="item" id={1} >
                        <div className="wrapper"><a href="#" id={1}><h3>{data["Nombre completo"]}</h3></a>
                            <figure>{ixx} Birch Street</figure>
                        </div>
                    </div>
                </li>
            });
        }
        this.setStates={datos:get_datos()}

    console.log(nextProps.resultados);

    }
  render() {







    return (
        <ul className="results list">
            { this.states.datos}
        </ul>
    );
  }
}

export default Resultados;



