import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import Resultados from './Resultados';
import customData from './data.json';
import registerServiceWorker from './registerServiceWorker';
var markers = [];



var container = document.getElementById('resultados')
var render = ReactDOM.render(<Resultados resultados={[]} />, container);
const hola = function (x) {

    console.log(window.map )
	var data =[];
	var add1,add2,add3;

    for (let dato of customData){

		add1 = false;
		add2 = false;
		add3 = false;
    	for (let ani of x.ano){

    		if( +dato["AÑO"] == ani){
    			add1 =true;
    		}
    	}
    	 if(x.ano.length ==0  ){
    		add1 = true;
    	}

    	for (let COMUNA of x.comuna){

    		if( dato["COMUNA"] == COMUNA){
    			add2 =true;
    		}
    	}
    	if(x.comuna.length ==0  ){
    		add2 = true;
    	}

    	for (let colegio of x.colegio){

    		if( dato["ESTABLECIMIENTO"] == colegio){
    			add3 =true;

    		}
    	}
    	if(x.colegio.length ==0  ){
    		add3 = true;
    	}


    	if (add1 && add2 && add3){
    		data.push(dato)
            if (dato.latitude!="0" && dato.latitude!="" ){

                var myLatLng = {lat: +dato.latitude, lng: +dato.longitude};


                var marker = new window.google.maps.Marker({
                    position: myLatLng,
                    map: window.map,
                    title: 'Hello World!'
                });



            }
    	}
    }




    ReactDOM.unmountComponentAtNode(container);
    render = ReactDOM.render(<Resultados resultados={data} />, container);


}

ReactDOM.render(<App hola ={hola.bind(this)}/>, document.getElementById('root'));
registerServiceWorker();



